import React, { Component } from "react";
import Breadcrumb from "../../common/breadcrumb/breadcrumb";
import Helmet from "react-helmet";
import { Parallax } from "react-parallax";
import Heading from "../../common/heading/heading";
import "./locations.scss";
import OurTeamBlock from "../../common/our-team/our-team";
import CallToActionSection from "../../sections/caltoaction/caltoaction";
import OurTeamSection from "../../sections/our-team/our-team";

class Locations extends Component {
  render() {
    return (
      <div className="about-page-wrap locations">
        <Helmet>
          <title>Raa residence | Lokacija</title>
        </Helmet>
        <Breadcrumb title={"Lokacija"} />

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12 content-left pl-md-3">
                <Heading className="col-lg-10" />
                <p className="col-lg-10 col-md-12 col-sm-12 text-black">
                  Stambeno-poslovni objekat Raa Residences se nalazi u ulici
                  Vojvode Pavla Cukia br 7.
                  <br /> <br />
                  Ovaj objekat predstavlja novo moderno jezgro u samom centru
                  grada.
                  <br /> <br />
                  Poslovno stambeni objekat se nalazi na 500m udaljenosti od
                  optine Raa i 800m od Karaorevog doma.
                </p>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-1 d-flex2 locationWrapper">
                <img
                  className="mx-md-auto mx-lg-unset d-block img-fluid ml-auto"
                  src="/assets/images/locations/lokacija_1.jpg"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center ">
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="mx-md-auto mx-lg-unset d-block img-fluid "
                  src="/assets/images/locations/lokacija_2.jpg"
                  alt=""
                />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 content-right">
                <Heading className="col-lg-10" />
                <p className="col-lg-11 col-md-12 col-sm-12 text-black">
                  Stambeno poslovna zgrada Raa Residences se sastoji od
                  poslovnog prostora i 42 stana u ulici Vojvode Pavla Cukia,
                  dok je parking prostor planiran u zadnjem delu dvorita sa 22
                  parking mesta.
                  <br /> <br />
                  Objekat je spratnosti Su+Pr+4sp+Pk(Ps), u objektu su planirani
                  stanovi razliitih struktura od garsonjera do etvorosobnih
                  stanova, povrine od 27.44m2 do 104.20m2.
                  <br /> <br />U zgradi postoji putniki lift nosivosti 630kg
                  ili osam osoba. U prizemlju se nalazi ulaz za stanare zgrade
                  iz ulice Vojvode Pavla Cukia i poslovni prostor povrine
                  111m2. <br /> <br /> Bruto povrina objekata je 3685.89m2.
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12 content-left pl-md-3">
                <Heading className="col-lg-10 mb-2" />
                <p className="col-lg-11 col-md-12 col-sm-12 text-black">
                  - Prozori sa dvoslojnim nisko-emisionim staklom koje absorbuje toplotu, bele boje sa aluminijumskom roletnom <br /><br />

                  - Podovi u sobama stanova su od laminata modernog dezena
                  evropskog hrasta.
                  <span className="d-block mt-3">
                    - U kupatilama, kuhinjama i na terasama podovi su izraeni
                    od italijanskih granitnih ploica.
                  </span>
                  <span className="d-block mt-3">
                    - Sanitarije i vodovodne instalacije u kupatilima su
                    izraene od italijanskog i domaeg programa.
                  </span>
                  <span className="d-block mt-3">
                    - Unutranja vrata su od farbanog medijapana sa izolacijom.
                  </span>
                  <span className="d-block mt-3">
                    - Ulazna vrata stanova su sigurnosna sa multi-point sistemom
                    zakljuavanja.
                  </span>
                  <span className="d-block mt-3">
                    - Spoljna stolarija je od vrhunskih esto-komornih PVC
                    profila u beloj boji sa okovom renomiranih proizvoaa.
                  </span>
                </p>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="mx-md-auto mx-lg-unset d-block img-fluid"
                  src="/assets/images/locations/lokacija_3.jpg"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="mx-md-auto mx-lg-unset d-block img-fluid"
                  src="/assets/images/locations/lokacija_4.jpg"
                  alt=""
                />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 content-right">
                <Heading className="col-lg-10" />
                <p className="col-lg-10 col-md-12 col-sm-12 text-black">
                  Svaki od stanova poseduje sistem daljinskog grejanja.
                  <br /> <br />
                  Fasadni zidovi su termiki izolovani od najsavremenijih
                  izolacionih ploa koji pruaju vrhunsku energetsku efikasnost
                  vaeg doma.
                  <br /> <br />
                  Fasada je zavrno obraena kombinacijom drveta i dekorativnog
                  fasadnog maltera.
                  <br /> <br />
                  Krovni pokriva je izraen od crepa.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Locations;
