import React, { Component } from "react";
import Heading from "../../common/heading/heading";

class OverviewFluidSection extends Component {
  render() {
    return (
      <div className="container-fluid px-md-4">
        <div className="row mx-lg-4">
          <div className="col-lg-6 col-md-12 col-sm-12 align-self-center">
            <div className="w-lg-75 mx-auto">
              <Heading>UDOBNOST IVOTA</Heading>
              <p className="text-black mt-5">
                Smeten u srcu mirnog porodinog naselja koje se razvija u novi,
                moderan deo grada, objekat spaja harmoninu celinu neuporediv
                oseaj topline i udobnosti i sve pogodnosti savremenog
                stanovanja.
              </p>
              <p className="mt-4">
                Sve to vam je vano je na dohvat ruke, a jedna od najveih
                prednosti ove lokacije je njena izuzetna povezanost sa lokalnim
                okruenjem i dostupnosti sa svim to vam je potrebno. Zato lako
                i brzo stiete od posla do stana i od stana do slobodnog vremena
                provedenog u prirodi, odmoru ili druenju sa prijateljima, koja
                nam se usled brzine tempa gradskog ivota ponekad zvue kao
                nedostian san.
              </p>
            </div>
          </div>
          <div className="col-lg-6 col-md-12 col-sm-12 align-self-center text-center">
            <img
              className="img-fluid"
              src="/assets/images/gallery1/01.jpg"
              alt=""
            />
          </div>
        </div>
      </div>
    );
  }
}

export default OverviewFluidSection;
