import React, { Component } from "react";
import Breadcrumb from "../../common/breadcrumb/breadcrumb";
import Helmet from "react-helmet";
import { Parallax } from "react-parallax";
import Heading from "../../common/heading/heading";
import "./about.scss";
import OurTeamBlock from "../../common/our-team/our-team";
import CallToActionSection from "../../sections/caltoaction/caltoaction";
import OurTeamSection from "../../sections/our-team/our-team";

class AboutPage extends Component {
  render() {
    return (
      <div className="about-page-wrap">
        <Helmet>
          <title>Raa residence | O nama</title>
        </Helmet>
        <Breadcrumb title={"O nama"} />

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12 content-left pl-md-3">
                <Heading>O NAMA</Heading>
                <p className="text-black">
                  Kupovina stana nije samo investicija u budunost, ve i
                  investicija u stvaranje doma kao simbola sigurnosti. Zato je
                  vano imati poverenje u investitora, ija e vas strunost i
                  reputacija uiniti bezbrinim.
                </p>
               
                
               
                <p>
                Rastom i dobrim poslovanjem osigurati dobit za kompaniju.
                  Pristup svakom projektu karakteriu tri specifinosti - visok
                  kvalitet, konkurentna cena i potovanje rokova. Na ovaj nain
                  teimo da ouvamo svoju reputaciju posveenih i strunih
                  profesionalaca.
                </p>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="img-fluid"
                  src="/assets/images/about/family_1.jpg"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
        <div className="section-wrapper">
          <Parallax
            className={`section-parallax`}
            bgImage={`/assets/images/about/family_5.jpg`}
            bgImageAlt="the cat"
            strength={500}
          >
            <div className="about-parallax  position-relative">
              <div className="container">
                <div className="row">
                  <div className="col-lg-4 col-md-4 col-sm-4">
                    <div className="box-counter text-center text-white">
                      <div className="box-counter-wrap">
                        <div className="counter-nummer">KVALITET</div>
                        <div className="counter-text">
                          NA NAJVIEM NIVOU, KOJI ISPUNJAVA SVA VAA OEKIVANJA
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 col-sm-4">
                    <div className="box-counter text-center text-white">
                      <div className="box-counter-wrap">
                        <div className="counter-nummer">ISTRAJNOST</div>
                        <div className="counter-text">
                          DOSLEDNOST VAEG TRUDA
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 col-sm-4">
                    <div className="box-counter text-center text-white">
                      <div className="box-counter-wrap">
                        <div className="counter-nummer">DIZAJN</div>
                        <div className="counter-text">
                          PRILAGOEN SVIM VAIM POTREBAMA
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </Parallax>
        </div>
        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="img-fluid"
                  src="/assets/images/about/family_2.jpg"
                  alt=""
                />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 content-right">
                <Heading>VAA PRIA</Heading>
                <p>
                  Trudimo se da naim zaposlenima obezbedimo bezbedno radno
                  okruenje. Verujemo da se nezgode mogu preduprediti i
                  spreiti. Stoga, kompanija zapoljava obuene radnike sa
                  iskustvom i sprovodi rigorozne mere zatite na radu. Potpuno
                  okrenuti Vaem zadovoljstvu, pruamo Vam mogunosti da
                  izaberete stan po svojoj meri i u potpunosti ga prilagodite
                  svojim eljama.
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12 content-left pl-md-3">
                <Heading>VAE VREDNOSTI</Heading>
                <p className="text-black">In-House Dizajn</p>
                <p>
                  Okrenuti klijentima, stalno traganje za inovacijama u
                  funkcionalnosti stanova, traei da kupcima obezbedi ivotni
                  prostor koji to stvarno jeste. Izbor lokacija i inovativna
                  reenja ine da Vaa investicija vremenom dobija na vrednosti.
                </p>
                <p className="text-black">Sigurnost</p>
                <p>
                  Nekretnine su fizika imovina koja ima potencijal da stvara
                  prihod i profit u duem vremenskom periodu i retko kad
                  ostvaruju negativan prihod. One su prostor neophodan za
                  egzistenciju. U nekretninama se krije najvee nacionalno
                  blago.
                </p>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="img-fluid"
                  src="/assets/images/about/family_3.jpg"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>

        <div className="section-wrapper">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 col-md-12 col-sm-12">
                <img
                  className="img-fluid"
                  src="/assets/images/about/family_4.jpg"
                  alt=""
                />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 content-right">
                <Heading>VIZIJA</Heading>
                <p>
                  Svojom ponudom proizvoda i usluga za svakodnevnu upotrebu
                  stvoriti najbolje uslove za potroae. Uz strune i motivisane
                  radnike pruiti najbolju uslugu i prijatnu kupovinu
                  potroaima u ugodnom okruenju.
                </p>
                <p>Rastom i dobrim poslovanjem osigurati dobit za kompaniju.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AboutPage;
